import React, { Component } from "react";
import { cover, Slidersettings } from "../../../redux/constant";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";
class MovieSlider extends Component {
  constructor(props) {
    super(props);
    this.state = {
      Movie: []
    };
  }
  componentDidMount = () => {
    this.setState({
      Movie: cover
    });
  };
  render() {
    const setting = {
      ...Slidersettings,
      centerPadding: "20px",
      autoplay: true,
      lazyLoad: true,
      responsive: [...this.props.Responsive]
    };
    console.log(setting);
    const Movie = this.state.Movie.map((v, k) => {
      return (
        <div className="Poster" key={k}>
          <img src={v.dp} alt="" className="slide" />
          <div className="Content w-100">
            <footer>
              <div className="User">
                <img src="./img/msg.png" alt="" />
              </div>
              <p>
                10.5k <br /> Voted
              </p>
              <div className="Icons">
                <img src="./img/icon/down.png" className="img-fluid" alt="" />
              </div>
            </footer>
          </div>
        </div>
      );
    });

    return (
      <React.Fragment>
        <Slider {...setting}>{Movie}</Slider>
      </React.Fragment>
    );
  }
}

export default MovieSlider;
