import React, { Component } from 'react';
import SCHEDULE from "../Schedule/Container";
import $ from "jquery";
import { connect } from "react-redux";
class Leftbar extends Component {
    constructor(props) {
        super(props);
        this.state = {
        };
    }
    hideShowHandle = e => {
        const event = e.target;
        const dataId = event.getAttribute("dataId");
        const width = $(`#${dataId}`).css("width");
        const Parent = event.parentNode;
        let eventImg = event.getAttribute("src").split("icon")[1].split(".")[0];

        if (width !== "300px") {
            Parent.style.backgroundColor = "#70707070";
            eventImg = `./img/icon${eventImg}hover.png`;
            event.setAttribute("src", eventImg);
            $(`#${dataId}`).animate({
                width: "300px",
                padding: "0 10px",
            },
                { duration: 500 });
        }
        else {
            Parent.style.backgroundColor = "transparent";
            eventImg = `./img/icon${eventImg.split("hover")[0]}.png`;
            event.setAttribute("src", eventImg);
            $(`#${dataId}`).animate({
                padding: "0px",
                width: "0px",
            },
                { duration: 500 })
        }

    }
    render() {
        return (
            <React.Fragment>
                <div className="homeLeftbar d-md-flex d-none col-2">
                    <div className="Dp">
                        <img src="./img/dp.png" className="img-fluid" alt="" />
                    </div>
                    <p>{this.props.name}</p>
                    <span>San Francisco, CA</span>
                    <div className="Schedule">
                        <div className="Img" >
                            <img src="./img/icon/clock.png" dataId="ScheduleSteps" onClick={this.hideShowHandle} alt="" />
                        </div>
                        <SCHEDULE />
                    </div>
                    <div className="Schedule">
                        <div className="Img">
                            <img src="./img/icon/news.png" alt="" />
                        </div>
                    </div>
                    <div className="Schedule">
                        <div className="Img">
                            <img src="./img/icon/bell.png" alt="" />
                        </div>
                    </div>
                    <div className="Schedule">
                        <div className="Img">
                            <img src="./img/icon/friends.png" alt="" />
                        </div>
                    </div>
                    <div className="Schedule">
                        <div className="Img">
                            <img src="./img/icon/group.png" alt="" />
                        </div>
                    </div>
                    <div className="Schedule">
                        <div className="Img">
                            <img src="./img/icon/gift.png" alt="" />
                        </div>
                    </div>
                </div>
            </React.Fragment>
        );
    }
}
const mapStateToProps = state => {
    return {
        ...state.Login
    }
}
export default connect(mapStateToProps)(Leftbar);