import React from 'react';
import Step1 from "./Step1/Step";
import Step2 from "./Step2/Step";
import Step3 from "./Step3/Step";
class Container extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            steps: 1
        }
    }
    onNextStep = () => {
        this.setState(state => {
            state.steps += 1;
            return {
                steps: state.steps
            }
        })
    }
    onBackStep = () => {
        this.setState(state => {
            state.steps -= 1;
            return {
                steps: state.steps
            }
        })
    }
    render() {
        return (
            <React.Fragment>
                <div id="ScheduleSteps">
                    <i className="fas fa-caret-left"></i>
                    <div className="Content container">
                        <div className="row">
                            {this.state.steps === 1 ? <Step1 /> : ""}
                            {this.state.steps === 2 ? <Step2 Back={this.onBackStep} /> : ""}
                            {this.state.steps === 3 ? <Step3 Back={this.onBackStep} /> : ""}
                            <div className="col-12 btns">
                                {this.state.steps < 3 ?
                                    <button onClick={this.onNextStep}>Next</button>
                                    : <button>Set</button>
                                }
                            </div>
                        </div>
                    </div>
                </div>
            </React.Fragment>
        )
    }
}

export default Container;