import React, { Component } from "react";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";
export default class MultipleRows extends Component {
    constructor(props) {
        super(props);
        this.next = this.next.bind(this);
        this.previous = this.previous.bind(this);
        this.state = {
            Data: [1, 2, 3, 4, 5, 6, 7, 8, 9],
            current: 1
        }
    }
    next = () => {
        this.slider.slickNext()
        if (this.state.current < Math.ceil(this.state.Data.length / 4)) {
            this.setState(state => {
                state.current += 1;
                return {
                    current: state.current
                }
            })
        }
    }
    previous = async () => {
        if (this.state.current > 1) {
            await this.slider.slickPrev();
            this.setState(state => {
                state.current -= 1;
                return {
                    current: state.current
                }
            })
        }
    }
    render() {
        const slides = this.state.Data.map((v, k) => {
            return (
                <div className="Slider" key={k}>
                    <img src={`img/slider/${v}.png`} alt="" />
                </div>
            )
        })
        const settings = {
            infinite: false,
            className: "center",
            slidesToShow: 2,
            speed: 500,
            rows: 2,
            slidesPerRow: 1,
            slidesToScroll: 2,
            arrows: false,
            draggable: false
        }
        return (
            <React.Fragment >
                <div className="Arrows">
                    <span>Recommended</span>
                    <div className="Icons">
                        <div className="LeftArrow" onClick={this.previous}>
                            <i className="fas fa-chevron-left"></i>
                        </div>
                        <p>{this.state.current}/{Math.ceil(this.state.Data.length / 4)}</p>
                        <div className="RightArrow" onClick={this.next}>
                            <i className="fas fa-chevron-right"></i>
                        </div>
                    </div>
                </div>
                <div className="Recommended">
                    <Slider {...settings} ref={c => (this.slider = c)}>
                        {slides}
                    </Slider>
                </div>
            </React.Fragment >
        );
    }
}