import React, { Suspense, lazy } from "react";
import { connect } from "react-redux";
import { slideChange } from "../../redux/actions";
import {
  SliderVertical,
  verticalSliderParams
} from "../../redux/constant/index";
import $ from "jquery";
import { reviewHandle } from "../../redux/actions/index";
const Swiper = lazy(() => import("react-id-swiper"));
let x = false;
class VerticalSlider extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      slider: SliderVertical,
      paused: false
    };
  }
  componentDidMount = () => {};
  imgSlide = e => {
    if (x) {
      console.log();
      this.props.slideChanged(e);
      let chil = document.getElementsByClassName("swiper-slide-prev")[0];
      let nextChil = document.getElementsByClassName("swiper-slide-next")[0];
      if (window.innerWidth > 767) {
        chil.previousElementSibling.style.opacity = 0.2;
        nextChil.nextElementSibling.style.opacity = 0.2;
      }
    }
  };
  handleOnClickSlide = e => {
    if (
      e.target.parentNode.parentNode.classList.contains("swiper-slide-active")
    ) {
      let tempState;
      let review = document.querySelector(".ReviewHome");
      if (getComputedStyle(review).opacity === "0") {
        review.style.opacity = 1;
        review.style.zIndex = 5;
        tempState = true;
      }
      $("body").css("overflow", "hidden");
      this.setState({
        paused: tempState
      });
      let Data = $(e.target)
        .parent()
        .parent()[0]
        .getAttribute("data-swiper-slide-index");
      this.props.handleExpand(this.state.slider[Data]);
    }
  };

  render() {
    let cla = this;
    const params = {
      ...verticalSliderParams,
      autoplay: false,
      on: {
        slideNextTransitionStart: function(e) {
          let slid = this.slides;
          for (let i = 0; i < slid.length; i++) {
            const classNames = slid[i].className.split(" ");
            for (let j = 0; j < classNames.length; j++) {
              if (classNames[j] === "swiper-slide-active") {
                x = true;
                const slide = slid[i].childNodes[0].childNodes[0].innerHTML;
                const index = slid[i].getAttribute("data-swiper-slide-index");
                if (x) {
                  cla.imgSlide(`${slide.split(" ")[0]}.png`);
                  cla.props.handleExpand(cla.state.slider[index]);
                }
              }
            }
          }
        },
        slidePrevTransitionStart: function(e) {
          let slid = this.slides;
          for (let i = 0; i < slid.length; i++) {
            const classNames = slid[i].className.split(" ");
            for (let j = 0; j < classNames.length; j++) {
              if (classNames[j] === "swiper-slide-active") {
                x = true;
                const slide = slid[i].childNodes[0].childNodes[0].innerHTML;
                const index = slid[i].getAttribute("data-swiper-slide-index");
                if (x) {
                  cla.props.handleExpand(cla.state.slider[index]);
                  cla.imgSlide(`${slide.split(" ")[0]}.png`);
                }
              }
            }
          }
        }
      }
    };
    const slides = this.state.slider.map((v, k) => {
      return (
        <div className="items" key={k}>
          <span>
            <b onClick={this.handleOnClickSlide}>{v.name}</b>
          </span>
          <div className="d-flex align-items-center flex-wrap">
            <i className="flaticon-binocular" />
            <i className="flaticon-binocular" />
            <i className="flaticon-binocular" />
            <i className="flaticon-binocular" />
            <span className="icon">
              <i className="flaticon-binocular" />
            </span>
            <div className="Rating">
              <p>7.5 / 10</p>
              <p>12045</p>
            </div>
            <div className="Rating PG d-flex d-md-none">
              <p>PG-13 / SB -18</p>
            </div>
          </div>
          <div className="Head d-none">
            <span>
              Staring:
              <span className="mx-2" />
              {v.starting}
            </span>
            <span>
              <b>Genres:</b>
              <span className="mx-2" />
              {v.Genres}
            </span>
            <span>{v.desc}</span>
          </div>
        </div>
      );
    });
    return (
      <div className="Slide">
        <Suspense fallback={""}>
          <Swiper
            {...params}
            getSwiper={e => {
              if (e !== undefined && e !== "" && e !== null) {
                if (this.state.paused) {
                  e.autoplay.stop();
                } else {
                  e.autoplay.start();
                }
              }
            }}
          >
            {slides}
          </Swiper>
        </Suspense>
      </div>
    );
  }
}
const mapStateToProps = state => {
  return {
    ...state
  };
};
const mapDispatchToProps = dispatch => {
  return {
    slideChanged: e => {
      dispatch(slideChange(e));
    },
    handleExpand: e => {
      dispatch(reviewHandle(e));
    }
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(VerticalSlider);
