import React from 'react'
class Time extends React.Component {
    state = {
        minute: 0,
        hours: 0,
        prefix: ""
    }
    onMinuteIncrease = () => {
        if (this.state.minute < 59) {
            this.setState(state => {
                state.minute += 1;
                return {
                    minute: state.minute
                }
            })
        }
        else {
            if (this.state.hours < 12) {
                this.setState(state => {
                    state.hours += 1;
                    return {
                        hours: state.hours,
                        minute: 0
                    }
                })
            }
            else {
                this.setState(state => {
                    state.hours = 0;
                    return {
                        hours: state.hours,
                        minute: 0
                    }
                })
            }
        }
    }
    onMinuteDecrease = () => {
        if (this.state.minute > 0) {
            this.setState(state => {
                state.minute -= 1;
                return {
                    minute: state.minute
                }
            })
        }
        else {
            if (this.state.hours !== 0) {
                this.setState(state => {
                    state.hours -= 1;
                    return {
                        minute: 59,
                        hours: state.hours
                    }
                })
            }
        }
    }
    UpdateState = (e) => {
        if (!isNaN(e.target.value)) {
            if (e.target.value <= 12) {
                this.setState({
                    [e.target.name]: parseInt(e.target.value)
                })
            }
        }
        else {
            this.setState({
                [e.target.name]: e.target.value
            })
        }
    }
    render() {
        return (
            <React.Fragment>
                <p>Choose a Time</p>
                <div className="Time">
                    <div className="TimeLine">
                        <i className="fas fa-stopwatch"></i>
                        <span>Set Time</span>
                        <div className="Input">
                            <input type="number" name="hours" maxLength="2" className="Hours" value={this.state.hours < 10 ? "0" + this.state.hours : this.state.hours} onChange={this.UpdateState} />
                            :
                            <input type="number" maxLength="2" className="Hours" name="minute" value={this.state.minute < 10 ? "0" + this.state.minute : this.state.minute} onChange={this.UpdateState} />
                            <div className="counter">
                                <i className="fas fa-caret-up" onClick={this.onMinuteIncrease}></i>
                                <i className="fas fa-caret-down" onClick={this.onMinuteDecrease}></i>
                            </div>
                        </div>
                        <div className="AMPM">
                            <input type="radio" className="d-none" name="prefix" id="AM" value="AM" onClick={this.UpdateState} defaultChecked />
                            <label htmlFor="AM">AM</label>
                            <input type="radio" className="d-none" value="PM" name="prefix" id="PM" onClick={this.UpdateState} />
                            <label htmlFor="PM">PM</label>
                        </div>
                    </div>
                </div>
            </React.Fragment>
        )
    }
}

export default Time;