import React, { Component } from "react";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
class Review extends Component {
  constructor(props) {
    super(props);
    this.state = {
      Content: []
    };
  }
  componentWillReceiveProps = Props => {
    this.setState({
      Content: Props.ReviewExpand.Data
    });
  };
  onCloseHandle = e => {
    let review = document.querySelector(".ReviewHome");
    review.style.zIndex = -1;
    review.style.opacity = 0;
  };
  render() {
    const review = this.state.Content.map((v, k) => {
      return (
        <React.Fragment key={k}>
          <div className="col-md-9 col-8 Post">
            <div className="User">
              <div className="Votes">
                <img src="./img/icon/arrowUp.png" alt="" />
                <span>38</span>
                <img src="./img/icon/arrowDown.png" alt="" />
              </div>
              <img src="./img/msg.png" className="Dp" alt="" />
            </div>
            <div className="Post">
              <span className="Title">Untitled Thread</span>
              <span className="Desc">
                Untitled Post Desc<b>1 Day Ago</b>
              </span>
              <div className="Img">
                <img src="./img/cover/mile.png" alt="" />
                <div>
                  <p>{k + 1}</p>
                </div>
              </div>
            </div>
          </div>
        </React.Fragment>
      );
    });
    const Details = this.state.Content.map((v, k) => {
      let img;
      if (v.name) {
        img = v.name.split(" ")[0];
      }
      return (
        <React.Fragment key={k}>
          <div
            className="CoverPhoto"
            style={{
              backgroundImage: `url(../../../img/${img}.png)`,
              backgroundPosition: "center",
              backgroundSize: "cover"
            }}
            key={k}
          >
            <i className="fas fa-times" onClick={this.onCloseHandle}></i>
            <div className="Content">
              <span className="Heading">
                <b onClick={this.handleOnClickSlide}>{v.name}</b>
              </span>
              <div className="Head">
                <span>
                  <b>Staring:</b>
                  {v.starting}
                </span>
                <span>
                  <b>Genres:</b>
                  {v.Genres}
                </span>
                <span>{v.desc}</span>
              </div>
              <div className="btnGroup">
                <Link to="/" className="btn">
                  Go to the Movie Page
                </Link>
                <Link to="/" className="btn">
                  Go to the Message Board
                </Link>
              </div>
            </div>
          </div>
          <div className="timeLine">
            <div className="TimeLineContent container-fluid">
              <div className="row">
                {review}
                {review}
                {review}
                {review}
                {review}
              </div>
            </div>
          </div>
        </React.Fragment>
      );
    });
    return (
      <React.Fragment>
        <div className="ReviewHome container-fluid">
          <div className="row">{Details}</div>
        </div>
      </React.Fragment>
    );
  }
}
const mapStateToProps = state => {
  return {
    ...state
  };
};
const mapDispatchToProps = dispatch => {
  return {};
};
export default connect(mapStateToProps, mapDispatchToProps)(Review);
